.rigth-column {
  background-color: #fffff0;
  left: 100px;
  margin: 0 0 0 2px;
  width: 60%;
  overflow: hidden;
  text-align: left;
  padding-left: 2%;
  padding-right: 5%;
  padding-top: 3em;
  padding-bottom: 3em;

 

  .contactComp {
    margin: 0;
    padding-left: 0;

    ul {
      margin: auto 0px;
      list-style-type: none;
      text-align: left;
      padding: 0;

      li {
        margin: 0;
        font-style: italic;
        font-weight: bold;

        a {
          text-decoration: none;
        }
        a:hover {
          color: #ff9900;
        }
      }
    }
  }

  .titleComp {
    margin-top: 2em;
    h1,
    h3 {
      margin: 0;
    }
    h1 {
      font-size: 45px;
      font-weight: bold;
    }
    h3 {
      font-size: 32px;
      font-style: oblique;
    }
  }
}
